import { Component, Inject, OnInit } from "@angular/core";
import { Validators } from "@angular/forms";
import { ActivatedRoute } from "@angular/router";
import { TranslateService } from '@ngx-translate/core';
import { ManageStandAloneLoadingService } from '@tei/core';
import { BreadCrumb, SnackBarService } from "@tei/material";
import { DesignationBasedSettingsModel, DesignationBasedSettingsValidators } from "@tei/material-smart-components";
import { GetClubByUUIDResponseClub } from "../../../@intermediate/clubs/model/get-clubs.model";
import { CreateClubRequest } from '../../../@intermediate/clubs/model/post-clubs.model';
import { ClubsService } from '../../../@services/clubs.service';

@Component({
  selector: "tei-tee-sheet-administration-clubs-edit-club",
  templateUrl: "./edit-club.component.html",
  styleUrls: ["./edit-club.component.scss"]
})
export class EditClubComponent implements OnInit
{
  isLoading = false;
  ClubID: string;
  breadCrumbData: BreadCrumb[] = [
    {
      title: this.translate.instant('TSADMINISTRATION.NAVIGATION.TEE_SHEET'),
      relativeUrl: '../../../../'
    },
    {
      title: this.translate.instant('TSADMINISTRATION.NAVIGATION.SETUP_AND_CONFIGURATION'),
      relativeUrl: '../../../'
    },
    {
      title: this.translate.instant('TSADMINISTRATION.NAVIGATION.COURSE_SETUP'),
      relativeUrl: '../../'
    },
    {
      title: this.translate.instant("TSADMINISTRATION.NAVIGATION.CLUBS"),
      relativeUrl: "../.."
    },
    {
      title: this.translate.instant("TSADMINISTRATION.CLUBS.EDIT_CLUB"),
      relativeUrl: ""
    }
  ];
  settings: DesignationBasedSettingsModel = {};
  clubData: GetClubByUUIDResponseClub;
  validators: DesignationBasedSettingsValidators = {
    Number: [Validators.required, Validators.min(1)]
  };

  constructor(private route: ActivatedRoute, @Inject("IClubsService") private clubsService: ClubsService,
    private snackbarService: SnackBarService,
    private translate: TranslateService,
    public manageStandAloneLoadingService: ManageStandAloneLoadingService) { }

  ngOnInit(): void
  {
    this.route.paramMap.subscribe(params =>
    {
      this.ClubID = params.get('ClubID') ?? '';
      if (this.ClubID)
      {
        this.loadClub();
      }
    });
    // Optionally set up breadcrumbs here
  }


  loadClub()
  {
    this.clubsService.GetClubByUUID(this.ClubID).subscribe((club) =>
    {
      this.clubData = club.Club;
      this.settings = club.Club.Settings;
    });
  }

  onSaveClub(formData: CreateClubRequest): void
  {
    // Type guard for sanitized image object
    function isSanitizedImage(obj: unknown): obj is { changingThisBreaksApplicationSecurity: string; }
    {
      return (
        obj !== null &&
        typeof obj === "object" &&
        "changingThisBreaksApplicationSecurity" in obj &&
        typeof (obj as any).changingThisBreaksApplicationSecurity === "string"
      );
    }

    let imageValue: string = "";
    const imageObj = formData?.Image;
    if (isSanitizedImage(imageObj))
    {
      imageValue = imageObj.changingThisBreaksApplicationSecurity;
    } else if (imageObj !== null && imageObj !== undefined)
    {
      imageValue = String(imageObj);
    }
    const payload = {
      ...formData,
      ClubUID: this.clubData?.ClubUID,
      Image: imageValue
    };

    if (payload.Image == "")
    {
      delete payload.Image;
      payload.ProfilePhotoUID = this.clubData?.ProfilePhotoUID;
    }

    this.isLoading = true;
    this.clubsService.PostClub(payload).subscribe({
      next: () =>
      {
        this.snackbarService.showSuccess(this.translate.instant("TSADMINISTRATION.GENERAL.SUCCESS_MESSAGE"), '', null, 'left');
        this.isLoading = false;
      },
      error: () =>
      {
        this.isLoading = false;
      }
    });
  }
} 